import "./flexMenu.css";

import util from "./util.js";

const flexboxMenu = {
  build(menuContainer, data) {
    this._menuContainer = menuContainer;
    menuContainer.innerHTML = "";

    this._flexMenu = document.getElementById("flexMenu");
    if (!this._flexMenu) {
      this._flexMenu = this._createFlexMenu();
      menuContainer.appendChild(this._flexMenu);
    }

    this._data = util.sortData(data.slice(), "name", "ASC");
    this._updateMenuItems(this._data);
    this._flexMenu.addEventListener("click", e => this.onClick(e));
  },

  onClick(e) {
    let target = e.target;
    if (!target.classList.contains("flexMenu__item-header")) {
      return;
    }
    let sortAttr = target.classList.contains("flexMenu__item-name") ? "name" :
                   target.classList.contains("flexMenu__item-price") ? "price" : null;
    let dir = target.getAttribute("data-dir") || "ASC";
    dir = dir == "ASC" ? "DSC" : "ASC";
    if (sortAttr) {
      this._data = util.sortData(this._data, sortAttr, dir);
      target.setAttribute("data-dir", dir);
      this._updateMenuItems(this._data);
    }
  },

  _updateMenuItems(data) {
    window.requestAnimationFrame(() => {
      let items = Array.from(this._flexMenu.children);
      items.shift();
      let dataNum = data.length;
      let itemNum = items.length;
      for (; dataNum > 0, itemNum > 0; --dataNum, --itemNum) {
        let i = data.length - dataNum;
        let j = items.length - itemNum;
        items[j] = this._safeInsertMenuData(items[j], data[i]);
      }

      if (dataNum < itemNum) {
        for (; itemNum > 0; --itemNum) {
          let j = item.length - itemNum;
          items[j].remove();
        }
      } else if (dataNum > itemNum) {
        let frag = document.createDocumentFragment();
        for (; dataNum > 0; --dataNum) {
          let i = data.length - dataNum;
          frag.appendChild(this._createMenuItem(data[i]));
        }
        this._flexMenu.appendChild(frag);
      }
    });
  },

  _createFlexMenu() {
    let ul = document.createElement("ul");
    ul.id = "flexMenu";
    ul.innerHTML = `
      <li class="flexMenu__item">
        <div class="flexMenu__item-name flexMenu__item-header">Name</div>
        <div class="flexMenu__item-desc flexMenu__item-header">Description</div>
        <div class="flexMenu__item-price flexMenu__item-header">Price</div>
        <div class="flexMenu__item-img flexMenu__item-header">Image</div>
      </li>
    `;
    return ul;
  },

  _createMenuItem(data) {
    let li = document.createElement("li");
    li.classList.add("flexMenu__item");
    li.innerHTML = `
      <div class="flexMenu__item-name flexMenu__item-attr"></div>
      <div class="flexMenu__item-desc flexMenu__item-attr"></div>
      <div class="flexMenu__item-price flexMenu__item-attr"></div>
      <div class="flexMenu__item-img flexMenu__item-attr"></div>
    `;
    return this._safeInsertMenuData(li, data);
  },

  _safeInsertMenuData(item, { name, desc, price, img, link }) {
    item.children[0].innerHTML = "";
    item.children[1].innerHTML = "";
    item.children[2].innerHTML = "";
    item.children[3].innerHTML = "";

    link = link.trim();
    if (link.indexOf("javascript") != 0) {
      let a = document.createElement("a");
      a.href = link;
      a.textContent = name;
      item.children[0].appendChild(a);
    } else {
      item.children[0].textContent = name;
    }

    item.children[1].textContent = desc;
    item.children[2].textContent = price;

    let imgElem = document.createElement("img");
    imgElem.src = img;
    item.children[3].appendChild(imgElem);

    // item.children[3].innerHTML = "<img src='" + img + "'>";

    return item;
  }
};

export default flexboxMenu;