const util = {

  sortData(data, attr, dir) {
    let cmp = null;
    dir = dir == "ASC" ? 1 : -1;
    switch (attr) {
      case "name":
        cmp = (a, b) => a.name.localeCompare(b.name) * dir;
        break;

      case "price":
        cmp = (a, b) => (a.price - b.price) * dir;
        break;

      default:
        return data;
    }
    return data.sort(cmp);
  },
  
};

export default util;
