import "./flexMenu.css";

import util from "./util.js";

const gridMenu = {
  build(menuContainer, data) {
    this._menuContainer = menuContainer;
    menuContainer.innerHTML = "";

    this._gridMenu = document.getElementById("grideMenu");
    if (!this._gridMenu) {
      this._gridMenu = this._createGirdMenu();
      menuContainer.appendChild(this._gridMenu);
    }

    this._data = util.sortData(data.slice(), "name", "ASC");
    this._updateMenuItems(this._data);
  },
  
  _createGirdMenu() {
    let article = document.createElement("article");
    article.id = "gridMenu";
    article.innerHTML = `
      <section class="gridMenu__headers">
        <div class="gridMenu__header gridMenu__header--name">Name</div>
        <div class="gridMenu__header gridMenu__header--desc">Description</div>
        <div class="gridMenu__header gridMenu__header--price">Price</div>
        <div class="gridMenu__header gridMenu__header--img">Image</div>
      </section>
      <ul class="gridMenu__body">
      </ul>
    `;
    return ul;
  },

  _createMenuItem({ name, desc, price, img, link }) {
    let item = document.createElement("li");
    item.classList.add("gridMenu__item");
    item.innerHTML = `
      <div class="gridMenu__item-attr gridMenu__item--name"></div>
      <div class="gridMenu__item-attr gridMenu__item--desc"></div>
      <div class="gridMenu__item-attr gridMenu__item--price"></div>
      <div class="gridMenu__item-attr gridMenu__item--img"></div>
    `;

    link = link.trim();
    if (link.indexOf("javascript") != 0) {
      let a = document.createElement("a");
      a.href = link;
      a.textContent = name;
      item.children[0].appendChild(a);
    } else {
      item.children[0].textContent = name;
    }

    item.children[1].textContent = desc;
    item.children[2].textContent = price;

    let imgElem = document.createElement("img");
    imgElem.src = img;
    item.children[3].appendChild(imgElem);

    return item;
  },

  _updateMenuItems(data) {
    window.requestAnimationFrame(() => {
      let menuBody = this._gridMenu.querySelctor(".gridMenu__body");
      let frag = document.createDocumentFragment();
      data.forEach(d => frag.appendChild(this._createMenuItem(d)));
      menuBody.innerHTML = "";
      menuBody.appendChild(frag);
    });
  }
};

export default gridMenu;
