import "./index.css";

import data from "./data";
import gridMenu from "./gridMenu.js";
import flexboxMenu from "./flexboxMenu.js";

const app = {
  init() {
    this._menuContainer = document.getElementById("menu-container");

    document.addEventListener("click", e => {
      switch (e.target.id) {
        case "menu-nav__grid":
          gridMenu.build(this._menuContainer, data);
        break;

        case "menu-nav__flexbox":
          flexboxMenu.build(this._menuContainer, data);
        break;
      }
    });
  }
};

app.init();
